import {useState,memo} from "react"

const InputIncome=memo((props)=>{
    const {number,deleteIncomeInput,addIncomeList}=props
    const [open,setOpen]=useState(true)
    const [isInputed,setIsInputed]=useState(false)
    const [type,setType]=useState("retire")
    const [charge,setCharge]=useState(0)
    const [count,setCount]=useState(0)
    const [type2,setType2]=useState("every")


    const decisionThisIncome=()=>{
        setIsInputed(true)
        addIncomeList(number,type,charge,count,type2)
    }

    const deleteThisIncome=()=>{
        setOpen(false)
        deleteIncomeInput(number)
    }

    let typeLabel1;
    if(type==="retire"){
        typeLabel1=<label>退職金</label>
    }else if(type==="other"){
        typeLabel1=<label>その他収入</label>
    }else if(type==="yachin"){
        typeLabel1=<label>家賃収入</label>
    }else if(type==="nenkin"){
        typeLabel1=<label>個人年金</label>
    }else{
        typeLabel1=<label>その他</label>
    }

    let typeLabel2;
    if(type2==="every"){
        typeLabel2=<label>毎年</label>
    }else if(type2==="before"){
        typeLabel2=<label>まで毎年</label>
    }else if(type2==="after"){
        typeLabel2=<label>から毎年</label>
    }else{
        typeLabel2=<label>一回限り</label>
    }
    return(
        <>  
            {open ?
            <>
                {isInputed ?
                    <>  
                        <label>種類:</label>
                        <label>{typeLabel1}</label>
                        <label>金額（年額）:</label>
                        <label>{charge}</label>
                        <label>回数:</label>
                        <label>{count}</label>
                        <label>{typeLabel2}</label>
                        <button onClick={deleteThisIncome}>削除</button>
                        <br/>
                    </>
                :
                <>
                    <label>種類</label>
                    <select onChange={(e)=>{setType(e.target.value)}}>
                        <option value="retire">退職金</option>
                        <option value="other">その他収入</option>
                        <option value="yachin">家賃収入</option>
                        <option value="nenkin">個人年金</option>
                        <option value="nenkinW">個人年金配偶者</option>
                    </select>
                    <label>金額（年額）</label>
                    <input type="number" value={charge} onChange={(e)=>{setCharge(e.target.value)}}/>
                    <label>回数</label>
                    <input type="number" value={count} onChange={(e)=>{setCount(e.target.value)}}/>
                    <select onChange={(e)=>{setType2(e.target.value)}}>
                        <option value="every">毎年</option>
                        <option value="before">まで毎年</option>
                        <option value="after">から毎年</option>
                        <option value="one">一回限り</option>
                    </select>
                    <button onClick={decisionThisIncome}>決定</button>
                    <button onClick={deleteThisIncome}>削除</button>
                    <br/>
                </>
                }
            
            </>
            :null}
            
        </>
    )
})

export default InputIncome