import {useState,memo} from "react"

const InputHuyo=memo((props)=>{
    const {number,deleteHuyoInput,addHuyoList}=props
    const [open,setOpen]=useState(true)
    const [isInputed,setIsInputed]=useState(false)
    const [type,setType]=useState("wife")
    const [childAge,setChildAge]=useState(0)
    const [numberOfHuyo,setNumberOfHuyo]=useState(0)
    const [self,setSelf]=useState("0")


    const decisionThisHuyo=()=>{
        setIsInputed(true)
        addHuyoList(number,type,childAge,numberOfHuyo,Number(self))
    }

    const deleteThisHuyo=()=>{
        setOpen(false)
        deleteHuyoInput(number)
    }

    let typeLabel;
    if(type==="child"){
        typeLabel=<label>扶養~22歳</label>
    }else if(type==="wife"){
        typeLabel=<label>配偶者</label>
    }else if(type==="other"){
        typeLabel=<label>その他扶養</label>
    }else if(type==="sinior1"){
        typeLabel=<label>老人同居</label>
    }else{
        typeLabel=<label>老人別居</label>
    }
    return(
        <>  
            {open ?
            <>
                {isInputed ?
                    <>  
                        <label>種類:</label>
                        {typeLabel}
                        {type==="child" ?
                            <>
                            <label>年齢:</label>
                            <label>{childAge}歳</label>
                            </>
                        :
                            <>
                                <label>人数:</label>
                                <label>{numberOfHuyo}人</label>
                            </>
                        }
                        <label>対象:</label>
                        {self ?
                        <label>本人</label>
                        :
                        <label>配偶者</label>}
                        <button onClick={deleteThisHuyo}>削除</button>
                        <br/>
                    </>
                :
                <>
                    <label>種類</label>
                    <select onChange={(e)=>{setType(e.target.value)}}>
                        <option value="wife">配偶者</option>
                        <option value="child">扶養~22</option>
                        <option value="other">扶養その他</option>
                        <option value="senior1">老人(70歳以上)同居</option>
                        <option value="senior2">老人(70歳以上)別居</option>
                    </select>
                    {type==="child" ?
                        <>
                        <label>年齢</label>
                        <input type="text" value={childAge} onChange={(e)=>{setChildAge(e.target.value)}}/>
                        </>
                    :
                        <>
                            <label>人数</label>
                            <input type="text" value={numberOfHuyo} onChange={(e)=>{setNumberOfHuyo(e.target.value)}}/>
                        </>
                    }
                    <label>対象</label>
                    <select onChange={(e)=>{setSelf(e.target.value)}}>
                        <option value="0">本人</option>
                        <option value="1">配偶者</option>
                    </select>
                    <button onClick={decisionThisHuyo}>決定</button>
                    <button onClick={deleteThisHuyo}>削除</button>
                    <br/>
                </>
                }
            
            </>
            :null}
            
        </>
    )
})

export default InputHuyo