import {useState,memo} from "react"

const InputHuyo=memo((props)=>{
    const {number,deletePayInput,addPayList}=props
    const [open,setOpen]=useState(true)
    const [isInputed,setIsInputed]=useState(false)
    const [type,setType]=useState("house")
    const [charge,setCharge]=useState(0)
    const [count,setCount]=useState(0)
    const [type2,setType2]=useState("every")


    const decisionThisPay=()=>{
        setIsInputed(true)
        addPayList(number,type,charge,count,type2)
    }

    const deleteThisPay=()=>{
        setOpen(false)
        deletePayInput(number)
    }

    let typeLabel1;
    if(type==="house"){
        typeLabel1=<label>住宅ローン、家賃</label>
    }else if(type==="car"){
        typeLabel1=<label>車（ローン）</label>
    }else if(type==="nomal"){
        typeLabel1=<label>一般支出</label>
    }else if(type==="sisanTax"){
        typeLabel1=<label>固定資産税</label>
    }else if(type==="seimeiHoken"){
        typeLabel1=<label>生命保険</label>
    }else{
        typeLabel1=<label>その他</label>
    }

    let typeLabel2;
    if(type==="every"){
        typeLabel2=<label>毎年</label>
    }else if(type==="before"){
        typeLabel2=<label>まで毎年</label>
    }else if(type==="after"){
        typeLabel2=<label>から毎年</label>
    }else{
        typeLabel2=<label>一回限り</label>
    }
    return(
        <>  
            {open ?
            <>
                {isInputed ?
                    <>  
                        <label>種類:</label>
                        <label>{typeLabel1}</label>
                        <label>金額（年額）:</label>
                        <label>{charge}</label>
                        <label>回数:</label>
                        <label>{count}</label>
                        <label>{typeLabel2}</label>
                        <button onClick={deleteThisPay}>削除</button>
                        <br/>
                    </>
                :
                <>
                    <label>種類</label>
                    <select onChange={(e)=>{setType(e.target.value)}}>
                        <option value="house">住宅ローン、家賃</option>
                        <option value="car">車（ローン）</option>
                        <option value="nomal">一般支出</option>
                        <option value="sisanTax">固定資産税</option>
                        <option value="seimeiHoken">生命保険</option>
                        <option value="other">その他</option>
                    </select>
                    <label>金額（年額）</label>
                    <input type="number" value={charge} onChange={(e)=>{setCharge(e.target.value)}}/>
                    <label>回数</label>
                    <input type="number" value={count} onChange={(e)=>{setCount(e.target.value)}}/>
                    <select onChange={(e)=>{setType2(e.target.value)}}>
                        <option value="every">毎年</option>
                        <option value="before">まで毎年</option>
                        <option value="after">から毎年</option>
                        <option value="one">一回限り</option>
                    </select>
                    <button onClick={decisionThisPay}>決定</button>
                    <button onClick={deleteThisPay}>削除</button>
                    <br/>
                </>
                }
            
            </>
            :null}
            
        </>
    )
})

export default InputHuyo